@import url(https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
	margin: 0;
	font-family: -apple-system, 'Roboto', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-size: 16px;
	color: rgba(0,0,0,.9);
	background: #f5f5f5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img {
	max-width: 100%
}

.container {
	max-width: 1170px;
	width: 100%;
	margin: 0 auto;
	padding: 0 15px;
}

button {
	cursor: pointer;
}
.app-content {
  display: grid;
  grid-template-rows: 52px 1fr 60px;
  grid-template-columns: 1fr;
  /*grid-gap: 10px;*/

  grid-template-areas:
    "h h" /*строка header и 2 колонки*/
    "m m" /*строка main и 2 колонки*/
    "f f" /*строка footer и 2 колонки*/
  ;
}

.main {
  grid-area: m;
  display: grid;
  grid-gap: 20px;
  grid-template-rows: 1fr;
  grid-template-columns: 12fr;
  grid-template-areas:
  "content" /*строка*/
  ;
}

.content {
  grid-area: content;
  margin: 40px 0;
}
